  cursor : pointer;
}

.errortext {
  margin-bottom    : 12px;
  padding          : 10px;
  color            : rgb(145, 57, 57);
  background-color : rgb(232, 131, 131);
}

/* Change styles for cancel button and signup button on extra small screens */

@media
